body {
  background-color: rebeccapurple;
}

.container {
  height: 90vh;
  align-items: center;
  display: flex;
  justify-content: center;
  flex-direction: column;
}

.vc-main {
  display: flex;
  flex-direction: column;
}

form {
  display: flex;
  flex-direction: column;
  gap: 5px;
}

h1 {
  position: absolute;
  top: 1px;
  align-self: center;
}
.vc-input {
  padding: 10px;
  border-radius: 5px;
  border: none;
}
.vc-button {
  padding: 10px;
  border-radius: 5px;
  border: none;
  cursor: pointer;
}

p {
  bottom: 1px;
  position: absolute;
  right: 1px;
}

.link {
  color: black;
  text-decoration: none;
  cursor: pointer;
}

.link:hover {
  color: white;
}
